.first_screen {
  position: relative;
}

.first_screen:before {
  width: 100%;
  content: '';
  display: block;
  position: absolute;
  background: #fff url(../img/1.png) no-repeat;
  background-position: 450px 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}

.first_screen .wrapper {
  position: relative;
  max-height: 450px;
  padding-top: 55px;
  padding-bottom: 150px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.first_screen .wrapper i.close-video {
  font-style: normal;
  font-size: 14px;
  color: #777;
  position: absolute;
  right: 20px;
  margin-bottom: 10px;
  bottom: 100%;
  z-index: 9999;
  text-decoration: underline;
  display: none;
  cursor: pointer;
}

.first_screen .wrapper i.close-video.playing {
  display: block;
}

.first_screen .wrapper #video-player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  opacity: 0;
  z-index: 5000;
  transition: opacity 1s;
}

.first_screen .wrapper #video-player.playing {
  display: block;
}

.first_screen .wrapper #video-player.playing.visible {
  opacity: 1;
}

.first_screen .wrapper .imgWrap {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex: 1 1 50%;
  border: 1px type transparent;
  border-right: 1px solid #58ece0;
  min-height: 170px;
  position: relative;
  left: -34px;
}

.first_screen .wrapper .imgWrap:before {
  content: '';
  display: block;
  border: 1px solid #58ece0;
  border-bottom: transparent;
  width: 120%;
  height: 40px;
  position: absolute;
  top: -40px;
  left: -1px;
}

.first_screen .wrapper .imgWrap:after {
  content: '';
  display: block;
  border: 1px solid #58ece0;
  border-top: transparent;
  width: 120%;
  height: 40px;
  position: absolute;
  bottom: -40px;
  left: -1px;
}

.first_screen .wrapper .imgWrap:before,
.first_screen .wrapper .imgWrap:after {
  left: -20% !important;
  margin-left: -1px;
}

.first_screen .wrapper img {
  max-width: none;
  cursor: pointer;
  width: 68px;
  height: 68px;
  border-radius: 50%;
  margin-right: -34px;
  transition: transform .5s;
}

.first_screen .wrapper img:hover {
  transform: scale(1.3);
}

.first_screen .wrapper main {
  margin-top: 30px;
  flex: 0 1 auto;
}

.first_screen h1 {
  font-size: 3.75rem;
  background-color: #fff;
  padding: 5px 5px 5px 0;
  line-height: 3rem;
  margin: 45px 0 10px 0;
}

.first_screen h4 {
  font-size: 1.125rem;
