/* Slick Styles */

.slick-next {
  right: 57px !important;
  z-index: 1004 !important;
}

.slick-next:before {
  background: #fff url(../img/arrow-right.png) no-repeat;
  z-index: 1005;
}

.slick-next:after {
  content: 'Следующий';
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  display: none;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:after,
.slick-next.slick-disabled:after {
  display: none;
}

.slick-prev,
.slick-next {
  position: relative;
  width: 57px !important;
  height: 11px !important;
}

.slick-prev:before,
.slick-next:before {
  content: '' !important;
  font-size: 0;
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  top: 0;
  opacity: 1 !important;
  left: 0;
  height: 100%;
}

.slick-prev:after,
.slick-next:after {
  position: absolute;
  top: 100%;
  margin-top: 10px;
  margin-left: 10px;
  left: 0;
  font-size: 12px;
  color: #000;
}

.slick-prev {
  left: 57px !important;
  z-index: 1004 !important;
}

.slick-prev:before {
  background: #fff url(../img/arrow-left.png) no-repeat;
  z-index: 1005;
}

.slick-prev:after {
  content: 'Предыдущий';
}

.slick-slide img {
  margin: 0 auto 40px !important;
}

.slick-dots {
  bottom: -50px !important;
}

.slick-dots:after {
  content: '';
  height: 1px;
  background-color: #000;
  position: absolute;
  top: 50%;
  width: 30%;
  left: 35%;
  margin-top: 3px;
  /*.media(500px; {
				width: 50%;
				left: 25%;
			});*/
  /*text-align: center;
			*/
}

.slick-dots li.slick-active button {
  opacity: 1 !important;
}

.slick-dots li.slick-active button:before {
  opacity: 1 !important;
}

.slick-dots li button {
  border: 1px solid #000;
  width: 10px !important;
  height: 10px !important;
  border-radius: 50%;
  opacity: 0;
  transition: opacity .3s;
}

.slick-dots li button:before {
  content: '';
  /*top: 50% !important;
				left: 50% !important;
				margin-top: -5px !important;
				margin-left: -5px !important;*/
  width: 15px !important;
  height: 15px !important;
  background-color: #000;
  opacity: 1;
  border-radius: 50%;
}

/* End Slick Styles */

