/* Modal Windows */

figure.video {
  width: 75%;
  position: absolute;
  top: 5%;
  left: 12.5%;
  z-index: 1000;
  outline: none;
  border: none;
}

.relation {
  width: 100%;
  margin: auto;
  padding-top: 56.25%;
  position: relative;
  height: 0;
}

.video_frame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

figure.video img {
  position: absolute;
  top: -30px;
  right: 0;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  -moz-transition: .3s;
  transition: .3s;
}

figure.video img:hover {
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
  transform: scale(1.3);
}

figure.modal_windows {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.6);
  top: 0;
  left: 0;
  z-index: 1000;
}

.modal_windows h4 {
  font-size: 1rem;
  text-align: center;
  margin-bottom: 20px;
}

.modal_windows h2 {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 20px;
}

figure.modal_windows input,
figure.modal_windows button {
  display: block;
  width: 75%;
  outline: none;
  height: 40px;
  margin: 20px auto;
  padding-left: 10px;
}

figure.modal_windows button {
  color: #fff;
}

.modal {
  width: 50%;
  padding: 75px 5px;
  background-color: #fff;
  position: absolute;
  top: 5%;
  left: 25%;
  -webkit-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
  -moz-box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.4);
}

.modal img {
  position: absolute;
  right: 20px;
  top: 20px;
  cursor: pointer;
  -webkit-transition: .3s;
  -o-transition: .3s;
  -moz-transition: .3s;
  transition: .3s;
}

.modal img:hover {
  -webkit-transform: scale(1.3);
  -moz-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
  transform: scale(1.3);
}

.modal__button {
  font-size: 1.1rem;
  border: none;
  background-color: #000;
  outline: none;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  transition: background-color 0.5s;
  cursor: pointer;
}

.modal__button:hover {
  background-color: #fff;
  color: #000;
  border: 1px solid #000;
}

.closed {
  display: none;
}

.opened {
  display: block;
}

@keyframes heart-beat {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.3);
  }

  50% {
    transform: scale(0.9);
  }

  75% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}

.loaded .screen:before {
  left: -100%;
}

.loaded .screen:after {
  right: -100%;
}

.screen {
  overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.screen.disabled {
  display: none;
}

.screen:before,
.screen:after {
  content: '';
  position: absolute;
  display: block;
  width: 50%;
  position: fixed !important;
  background-color: rgba(255, 255, 255, 0.8);
  transition: 0.5s ease-in-out;
  top: 0;
  height: 100%;
  z-index: 9998;
}

.screen:before {
  left: 0;
}

.screen:after {
  right: 0;
}

.screen img {
  width: 200px;
  height: 200px;
  animation: heart-beat 2s infinite;
  transition: opacity .3s;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -100px;
  margin-left: -100px;
  z-index: 9999;
}

.screen img.loaded {
  opacity: 0;
}

.screen img.loaded.disabled {
  display: none;
}

